@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body {
  font-family: 'Libre Baskerville', serif;
  background-color: #e2fcea;
  color: black;
  letter-spacing: .07em;
}

.landing-top {
  
  height: 100vh;
  width: 100%;
}

.landing-background {
  z-index: 1;
  height: 100vh;
  width: 100vw;
  background-image: url(/static/media/background.37c62bb5.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.landing-header-container {
  height: 100%;
}

.landing-contact-container, .landing-contact-container a {
  
  height: 100%;
  color: #e2fcead2;
  transition-duration: .5s;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  color: rgb(255, 91, 255);
  text-decoration: none;
}

.name {
  font-size: 3.5rem;
  font-weight: 575;
}

.heading {
  
  font-size: 1.3rem;
  width: 60%;
  line-height: 1.5;
}

.about-page-row {
  height: 20vh;
  width: 100%;
}

.about-body {
  height: 100%;
  width: 100%;
  z-index: 1;
  /* background-color: #171915; */
  background-image: url(/static/media/aboutsection.ca51b322.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.projects-page-row {
  height: 100vh;
  position: relative;
  
}

.about-page-row {
  color: #e2fceaaf;
  letter-spacing: .07em;
  line-height: 2;
}



.landing-bot-container {
  height: 100vh;
  position: relative;
  background-color: white;
  background-image: url(/static/media/backtree.c9627689.png);
  background-repeat: no-repeat;
  background-size: cover;
}

@media only screen and (max-width: 1024px) {
  .name {
    font-size: 2.4em;
  }

  .heading {
    font-size: 1.0em;
  }
}

@media only screen and (max-width: 1240px) {
  .landing-contact {
    font-size: 1.0em;
  }

  .email, .github, .linkedin {
    font-size: .9em;
  }

}

@media only screen and (max-width: 1440px) and (max-height: 900px) {
  .heading {
    width: 80%;
  }

  .storybook-text {
    font-size: .8em;
  }
}

.particles-canvas {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  z-index: 0;
}
.storybook {
  /* box-shadow: 4px 4px 7px whitesmoke; */
  position: relative;
  background-color: rgb(250, 250, 250);
  z-index: 1;
}



.page-forward, .page-back, .page-contents {
  transition-duration: .5s;
}

.page-forward:hover, .page-back:hover, .page-contents:hover {
  color: rgba(67, 67, 179, 0.89);
}

/* .storybook > div > div > div > div> div {
  height: 100%;
} */

.storybook, .storybook::before, .storybook::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-right: 1px solid rgba(128, 128, 128, 0.267);
  border-bottom: 1px solid rgba(128, 128, 128, 0.267);
  border-top: 1px solid rgba(128, 128, 128, 0.267);
}

.storybook-font-weight {
  font-weight: lighter;
}

.subheader {
  color: rgba(0, 0, 0, 0.85);
}

.storybook::before {
  left: 3px;
  top: 2px;
  z-index: -1;
  background-color: rgb(247, 247, 247);
  
}

/* Third sheet of paper */
.storybook::after {
  left: 6px;
  top: 4px;
  z-index: -2;
  background-color: rgb(245, 245, 245);
}

.storybook-container {
  position: relative;
  background-color: none;
}

.toc-button {
  transition-duration: .5s;
}

.toc-button:hover {
  color: rgb(255, 91, 255);
}

a {
  transition-duration: .5s;
}

.proj-pic {
  width: 100%;
  
  background-color: white;
  box-shadow: 1px 1px 3px white;
  border: 2px solid gray;
}

.storybook-intro-page {

}

.bookPage {
  
}

article {
  z-index: 2;
  background-color: rgb(250, 250, 250);
  border-left: 1px solid rgba(128, 128, 128, 0.267);
  border-right: 1px solid rgba(128, 128, 128, 0.267);
  border-bottom: 1px solid rgba(128, 128, 128, 0.267);
  
}

.storybook-text {
  font-size: 1em;
  letter-spacing: .05em;
}

.storybook-header {
  font-size: 2em;
}

.prof-pic {
  background-color: white;
  box-shadow: 1px 1px 3px white;
  border: 2px solid gray;
}

.page-back {
  position: absolute; 
  left: 0;
}

.page-forward {
  position: absolute;
  right: 0;
}

@media only screen and (max-width: 1440px) and (max-height: 900px) {

  .storybook-font-light, .storybook-project-links, .storybook-text {
    font-size: .9em;
  }

  .storybook-project-desc {
    font-size: .8em;
    text-overflow: ellipsis;
    letter-spacing: normal;
  }

  .toc, .storybook-header {
    font-size: 1.7em;
  }

  .subheader {
    font-size: .7em;
  }

  .toc-button {
    font-size: 1.2em;
  }

  
  
}

@media only screen and (max-width: 1024px) {
  .storybook-text {
    font-size: .75em;
  }

  .toc-button {
    font-size: 1em;
  }

  .storybook-font-light, .storybook-project-links {
    font-size: .8em;
  }

  .storybook-project-desc {
    font-size: .7em;
    text-overflow: ellipsis;
  }

  .storybook-header {
    font-size: 1.3em;
  }

  
}

@media only screen and (max-height: 728px) {
  .prof-pic {
    padding: 1px!important;
    width: auto;
    max-height: 100%;
  }
}
.mobile-container {
  height: 100vh;
  z-index: 1;
  height: 100vh;
  width: 100vw;
  background-image: url(/static/media/background.37c62bb5.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.message {
  z-index: 1;
  background-color: white;
  border: 2px solid gray;
  box-shadow: 1px 1px 3px white;
}

.particles-canvas {
  z-index: 0;
}
